
import { Input, Popover, Radio, Modal, message } from "antd";
import {
  ArrowDownOutlined,
  DownOutlined,
  SettingOutlined,
} from "@ant-design/icons";
import React , {useState, useEffect} from "react";
import axios from "axios";




function Swap(){

    const [slippage, setSlippage] = useState(2.5);

    const [tokenOneAmount, setTokenOneAmount] = useState(null);
    const [tokenTwoAmount, setTokenTwoAmount] = useState(null);
 const [tokenList,setTokenList]= useState([]);
   const [tokenOne, setTokenOne] = useState(tokenList[0]);
    const [tokenTwo, setTokenTwo] = useState(tokenList[1]);
    const [isOpen,setIsOpen] = useState(false)
const [changeToken, setChangeToken] = useState(1);
const [prices,setPrices]= useState(null)



   const config = {
    headers: {
"Authorization": "Bearer Dc6PMiiozmna9v3hMqK2s1zFg9wB0CJs",
 "X-Requested-With": "XMLHttpRequest",

},
    params: {}
};


  const url = "https://cors-anywhere.herokuapp.com/https://api.1inch.dev/swap/v6.0/1/tokens";
//const url ="https://api.1inch.dev/swap/v6.0/1/tokens"

 const fetchTokenList = async () =>  {
 try {
    const response = await axios.get(url, config);
    console.log("token loaded",response.data?.tokens?.address);

    const tokens = Object.entries(response.data.tokens).map(([key, token]) => ({
      ...token,
      id: key
    }));


//    const theList = response?.data?.tokens;
    setTokenList(tokens);
    setTokenOne(tokens[0]);
    setTokenTwo(tokens[1]);
  } catch (error) {
    console.error(error);
  }   


};



const fetchTokenPrice = async (one,two) => {
  try {
    const response = await axios.get('/tokenPrice', {
      params: {
        addresses: {addressOne: one, addressTwo: two} // Replace with the actual token address
      }
    });
    setPrices(response.data);
  } catch (error) {
    console.error('Error fetching token price:', error);
  }
};






    useEffect(() => {
        fetchTokenList(); 
      //  fetchTokenPrice(tokenList[0].address,tokenList[0].address);
      fetchTokenPrice(tokenOne?.address,tokenTwo?.address)
    
    }, [])

    function handleSlippageChange(e) {
        setSlippage(e.target.value);
      }




      function switchTokens(){

const one = tokenOne;
const two = tokenTwo;

setTokenOne(two);
setTokenTwo(one);

      }

function openModal(asset){
  setChangeToken(asset);
  setIsOpen(true); 
}


function modifyToken(i){
  if (changeToken === 1){
    setTokenOne(tokenList[i]);
    
  } else {
    setTokenTwo(tokenList[i])
  }
  setIsOpen(false)
}

    const settings = (
        <>
          <div>Slippage Tolerance</div>
          <div>
            <Radio.Group value={slippage} onChange={handleSlippageChange}>
              <Radio.Button value={0.5}>0.5%</Radio.Button>
              <Radio.Button value={2.5}>2.5%</Radio.Button>
              <Radio.Button value={5}>5.0%</Radio.Button>
            </Radio.Group>
          </div>
        </>
      );



return (

<>
 <Modal
  open={isOpen}
  footer={null}
  onCancel={() => setIsOpen(false)}
  title="Select a token"
> 
<div className="modalContent">
  {tokenList?.map((e,i) => {
    return (
      <div 
      className="tokenChoice"
      key={i}
     onClick={() => modifyToken(i)} 
      >
        
<img src={e.logoURI} alt={e.symbol} className="tokenLogo" />
<div className="tokeChoiceNames">
  <div className="tokenName">{e.name}</div>
  <div className="tokenTicker">{e.symbol}</div>
</div>
</div>
    );
  })}
  </div>
</Modal>



    <div className="tradeBox">
    <div className="tradeBoxHeader">
      <h4>Swap</h4>
      <Popover
        content={settings}
        title="Settings"
        trigger="click"
        placement="bottomRight"
      >
        <SettingOutlined className="cog" />
      </Popover>
    </div>
    <div className="inputs">
        <Input
            placeholder="0"
           value={tokenOneAmount}
           // onChange={changeAmount}
            disabled={false}
          />
          <Input placeholder="0"  disabled={true} />
          <div className="switchButton" onClick={switchTokens} >
            <ArrowDownOutlined className="switchArrow" />
          </div>
          <div className="assetOne"  onClick={() => openModal(1)}>




          {tokenOne && (
            <>
              <img src={tokenOne.logoURI} alt="assetOneLogo" className="assetLogo" />
              <span>{tokenOne.symbol}</span>
            
            </>
          )}
         {/*   <img src={tokenOne?.logoURI} alt="assetOneLogo" className="assetLogo" />
            {tokenOne?.symbol} */}
            <DownOutlined />
          </div>
          <p>USD Price: {prices ? `$${prices}` : 'Loading...'}</p>
          <div className="assetTwo" onClick={() => openModal(2)}>
       


{tokenTwo && (
            <>
              <img src={tokenTwo.logoURI} alt="assetOneLogo" className="assetLogo" />
              <span>{tokenTwo.symbol}</span>
            
            </>
          )}
            <DownOutlined />
          </div>
</div>
</div>

</>
);

}

export default Swap;